    0%
    {
        width: 0;
    }
    100%
    {
        width: 100%;
    }
}

.skills
{
    font-size: 1.6rem;
    font-weight: 600;

    display: grid;

    margin-top: 1rem;

    text-shadow: 0 .2rem .1rem black; 

    grid-template-columns: repeat(3, -webkit-max-content);
    grid-template-columns: repeat(3, max-content);
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    grid-gap: 2rem;
}
.skills__box
{
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}
.skills__icon
{
    width: 2rem;
    height: 2rem;
    margin-right: .5rem;

    fill: #b0d100;
    -ms-flex-item-align: center;
    align-self: center;
}
.skills__icon--code
{
    width: 10rem;
    height: 10rem; 

    fill: #555;
}

.getintouch
{
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;

    width: 120rem;
    height: 50rem;
    margin-top: 1rem;
    margin-right: auto;
    margin-left: auto;

    background-color: white;
    -webkit-box-shadow: 0 5rem 7rem rgba(0, 0, 0, .9);
            box-shadow: 0 5rem 7rem rgba(0, 0, 0, .9);
}
.getintouch__map
{
    position: relative;

    width: 60%;
    height: 100%; 
    margin: 0;
    padding: 0;
}
.getintouch__form
{
    width: 40%;
    height: 100%;

    color: white;
    background-color: #111;
}

.form
{
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
        flex-direction: column;

    color: white; 

    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
}
.form__group
{
    position: relative;
}
.form__label,
.form__input,
.form__message,
.form__submit
{
    font-family: inherit;

    margin: 0;

    border: none; 
    outline: none;
}
.form__label
{
    font-family: 'Montserrat', sans-serif;
    font-size: 1.2rem;

    position: absolute;
    left: 5%;

    -webkit-transition: all .3s;
            transition: all .3s; 
    -webkit-transform: translateY(.5rem);
            transform: translateY(.5rem);

    color: #9bb700;
}
.form__input
{
    font-family: 'Lato', sans-serif !important;
    font-size: 1.6rem;

    width: 90%;
    padding: 2.5rem 1.7rem 1rem 0rem;

    -webkit-transition: all .5s;
            transition: all .5s; 

    color: currentColor;
    border: none;
    outline: none;
}
.form__input:placeholder-shown,
.form__message:placeholder-shown
{
    font-family: 'Montserrat', sans-serif !important;
}
.form__input:placeholder-shown + .form__label,
.form__message:placeholder-shown + .form__label,
.form__message:placeholder-shown:not(:focus) + .form__label
{
    visibility: hidden;

    -webkit-transform: translateY(1rem);
            transform: translateY(1rem); 

    opacity: 0;
}
.form__input,
.form__message
{
    font-family: 'Montserrat', sans-serif;
    font-size: 1.4rem;

    border-bottom: .1rem solid rgba(155, 183, 0, .1); 
    background-color: #111;
}
.form__input:focus,
.form__message:focus
{
    border-bottom: .1rem solid #b0d100; 
    outline: none;
}
.form__message
{
    font-family: 'Lato', sans-serif;
    font-size: 1.6rem;
    font-size: 1.4rem;

    width: 90%;
    height: 28rem;
    padding: 2.5rem 1.7rem 1rem 0rem;

    -webkit-transition: all .5s;
            transition: all .5s;

    color: currentColor;
    border: none;
    border-bottom: .1rem solid rgba(155, 183, 0, .3); 
    outline: none;
    background-color: #111;
}
.form__submit
{
    font-size: 1.6rem;
    font-weight: bold;

    display: inline-block;

    width: 90%;
    margin-top: 1rem;
    padding: 1.5rem 4rem 1.7rem !important;

    cursor: pointer;
    -webkit-transition: all .5s;
            transition: all .5s; 

    color: #3b3d40;
    border: none;
    outline: none;
    background-color: rgba(176, 209, 0, .75);
    text-shadow: 0 .1rem 0 rgba(255, 255, 255, .3);
}
.form__submit:hover
{
    background-color: #b0d100;
}
.form h4
{
    font-weight: normal;

    margin-top: 1rem;

    color: #ccc;
}

label.error
{
    font-family: 'Montserrat', sans-serif;
    font-size: 1.2rem;
    font-weight: bold;

    position: absolute;

    color: #c9483d;
}

label#fullname-error
{
    top: .5rem;
    left: 9.2rem;
}

label#email-error
{
    top: .5rem;
    left: 6.5rem;
}

label#message-error
{
    top: .6rem;
    left: 8rem;
}

#map
{
    position: absolute;
    top: 0;
    left: 0;

    width: 100%;
    height: 100%;
}

.hero
{
    position: relative; 

    height: 100vh;
}
.hero::before
{
    display: block;

    width: 100%;
    height: 100%;

    content: '';

    opacity: .35;
    background: url(./img/black_twill.png) center repeat; 
    background-size: 1.6rem 1.6rem;
}
.hero__heading
{
    position: absolute;
    top: 50%;
    left: 50%;

    width: 70%;

    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%); 
    text-align: center;

    color: white;
    text-shadow: 0 .3rem .1rem black;
}
.hero__heading h1
{
    font-family: 'Montserrat', sans-serif;
    font-size: 15.7rem;
    font-weight: 700;

    text-transform: uppercase; 

    color: white;
}
.hero__heading > span
{
    font-size: 4.95rem;
    font-weight: 300; 

    text-shadow: 0 .2rem .1rem black;
}
.hero__home
{
    font-family: 'Montserrat', sans-serif;

    width: 25%;
    margin-top: 15rem;
    padding: 2rem 4rem 2rem;

    -webkit-transition: all .3s;
            transition: all .3s; 
    text-align: center;
    text-transform: uppercase;

    color: #c9483d;
    border: .2rem solid #c9483d;
    border-radius: .5rem;
    outline: none;
}
.hero__home:hover
{
    -webkit-transform: scale(1.07);
            transform: scale(1.07);

    color: #f9f9f9;
    background-color: #c9483d;
    text-shadow: 0 1px 1px black;
}
.hero__home:focus
{
    -webkit-transform: scale(1);
            transform: scale(1);
}

#contact
{
    position: relative;

    background-image: -webkit-gradient(linear, left top, left bottom, from(rgba(0, 0, 0, .5)), to(rgba(0, 0, 0, .5))), url(./img/contact-bg.jpg);
    background-image: linear-gradient(to bottom, rgba(0, 0, 0, .5) 0%, rgba(0, 0, 0, .5) 100%), url(./img/contact-bg.jpg);
    background-position: center; 
    background-size: cover;
}
#contact h1
{
    word-spacing: .5rem;

    color: white;
    text-shadow: 0 .2rem .2rem black;
}

.contact
{
    text-align: center; 

    color: white;
}
.contact__info
{
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;

    margin-top: 5rem; 

    -ms-flex-pack: distribute;
    justify-content: space-around;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
}
.contact__info-items
{
    -webkit-transition: all .5s;
            transition: all .5s;
}
.contact__info-items:hover
{
    -webkit-transform: scale(1.15) translateY(-2rem);
            transform: scale(1.15) translateY(-2rem);
}
.contact__info-items:hover p
{
    font-weight: bold;
}
.contact__info-icon
{
    width: 6.5rem;
    height: 6.5rem;

    fill: #9bb700;
}
.contact__info-icon.-drop
{
    width: 2rem;
    height: 2rem;
    margin-right: .5rem; 

    -webkit-transform: translateY(4px);
            transform: translateY(4px);

    fill: #9bb700;
}
.contact__info-icon.-send
{
    width: 2rem;
    height: 2rem;

    fill: #121;
}
.contact__info p
{
    margin-top: .5rem;
}

.footer
{
    position: relative; 

    padding-top: 5rem;

    color: #777;
    background-color: #141414;
}
.footer__columns
{
    font-size: 1.4rem; 

    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;

    padding-bottom: 2.5rem;

    -webkit-box-pack: justify;
    -ms-flex-pack: justify;
    justify-content: space-between;
}
.footer__columns > div h5
{
    font-size: normal;

    margin-bottom: 1rem;

    text-align: left; 

    color: #b0d100;
}
.footer__columns > div ul
{
    list-style: none;
}
.footer__columns > div ul li > h5
{
    margin-top: 1.5rem;
    margin-bottom: 1rem;
}
.footer__columns > div ul li:not(:last-child)
{
    margin-bottom: .5rem;
}
.footer__story
{
    width: 30%;
}
.footer__story h1
{
    font-weight: normal;

    position: relative;

    margin-bottom: 1.5rem;

    -webkit-transition: all .2s;
            transition: all .2s; 
    text-transform: none;

    color: white;
}
.footer__story h1:after
{
    position: absolute;
    bottom: -.5rem;
    left: 50%;

    width: 100%;
    height: .15rem;

    content: '';
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);

    background-color: #bbde00;
}
.footer__about
{
    font-size: 1.6rem;

    margin-bottom: 1rem;

    text-align: justify;
    text-indent: 3rem;
}
.footer__about:nth-of-type(1)
{
    margin-top: 2.5rem;
}
.footer__social
{
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;

    padding-top: .5rem; 

    -webkit-box-pack: justify;
    -ms-flex-pack: justify;
    justify-content: space-between;
}
.footer__social-icon
{
    width: 2.5rem;
    height: 2.5rem;

    fill: #f9f9f9;
}
.footer__social li
{
    -webkit-transition: all .3s;
            transition: all .3s;
}
.footer__social li:not(:last-child)
{
    margin-right: .5rem;
}
.footer__social li:hover
{
    -webkit-transform: translateY(-.5rem);
            transform: translateY(-.5rem);
}

.copyright
{
    font-size: 1.2rem;

    margin-top: 2.5rem;
    padding-top: 2.5rem;
    padding-bottom: 2.5rem;

    text-align: center;

    border-top: .1rem solid rgba(51, 51, 51, .3); 
    background-color: #101010;
}
